import Vue from 'vue';
import VueRouter from 'vue-router';

import PageStage from '@/pages/stage/PageStage.vue';
import PageAuth from '@/pages/auth/PageAuth.vue';
import PageUserCreate from '@/pages/user-create/PageUserCreate.vue';
import PageUI from '@/pages/ui/PageUI.vue';
// для демонстрации верстки
import PageStage2 from '@/pages/stage/PageStage2.vue';
import PageReport from '@/pages/report/PageReport.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'stage',
    component: PageStage,
  },
  {
    path: '/auth',
    name: 'auth',
    component: PageAuth,
  },
  {
    path: '/user-create',
    name: 'user-create',
    component: PageUserCreate,
  },
  {
    path: '/ui',
    name: 'ui',
    component: PageUI,
  },
  // для демонстрации верстки
  {
    path: '/stage2',
    name: 'stage2',
    component: PageStage2,
  },
  {
    path: '/report',
    name: 'report',
    component: PageReport,
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
