import Vue from 'vue';
import VueFormGenerator from 'vue-form-generator';
import VModal from 'vue-js-modal';
import SvgIcon from 'vue-svgicon';
import vSelect from 'vue-select';

import router from '@/router';
import store from '@/store';

import App from '@/App.vue';
import AppSelect from '@/components/forms/app-select/AppSelect';
import AuthInput from '@/components/auth/auth-input/AuthInput';

// Дефолтные стили плагина форм, можно подключить, можно писать стили с нуля, как удобней
// import 'vue-form-generator/dist/vfg.css';
import '@/styles/index.scss';
import 'vue-select/src/scss/vue-select.scss';

Vue.config.productionTip = false;

Vue.use(VueFormGenerator);
Vue.use(VModal, { dynamic: true });
Vue.use(SvgIcon, {
  tagName: 'svg-icon',
});
Vue.component('v-select', vSelect);
Vue.component('fieldAppSelect', AppSelect);
Vue.component('fieldAuthInput', AuthInput);

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app');
