<template>
  <div class="auth-input" :class="className">
    <label :for="schema.id" class="auth-input__label">{{ schema.customLabel }}</label>
    <div class="auth-input__control-wrapper">
      <input
        v-model="value"
        :required="schema.required"
        :placeholder="schema.placeholder"
        :id="schema.id"
        :type="inputType"
        class="auth-input__control"
      />

      <component
        :is="iconComponent"
        class="auth-input__icon"
        :class="{ 'auth-input__icon--password': this.isPassword }"
        @click="viewPassword"
      >
        <svg-icon :name="iconName" width="16" height="16" />
      </component>
    </div>
  </div>
</template>

<script>
import { abstractField } from 'vue-form-generator';

import '@/assets/images/svg-generated/auth-user';
import '@/assets/images/svg-generated/auth-pass-close';
import '@/assets/images/svg-generated/auth-pass-open';

export default {
  mixins: [abstractField],
  data() {
    return {
      isPassword: false,
      isPasswordOpen: false,
    };
  },
  computed: {
    inputType() {
      if (this.schema.inputType === 'password') {
        return this.isPasswordOpen ? 'text' : 'password';
      }
      return this.schema.inputType;
    },
    className() {
      return {
        [this.schema.customClasses]: this.schema.customClasses,
        'is-full': this.value,
      };
    },
    iconName() {
      if (this.schema.inputType === 'password') {
        return this.isPasswordOpen ? 'auth-pass-open' : 'auth-pass-close';
      }
      return this.schema.iconName;
    },
    iconComponent() {
      return this.isPassword ? 'button' : 'span';
    },
  },
  methods: {
    viewPassword() {
      if (this.isPassword) {
        this.isPasswordOpen = !this.isPasswordOpen;
      }
    },
  },
  mounted() {
    if (this.schema.inputType === 'password') {
      this.isPassword = true;
    }
  },
};
</script>

<style src="./auth-input.scss" lang="scss"></style>
