import VueFormGenerator from 'vue-form-generator';

export default {
  data() {
    return {
      model: {},
    };
  },
  methods: {
    // Эту фунцкию нужно вызывать каждый раз при использовании vue-form-generator
    // (в mounted или другом удобном месте, для генерации модели (data))
    generateFormModel() {
      let model = {};

      Object.keys(this.schema).forEach(schemaKey => {
        const schemaObj = this.schema[schemaKey];
        const mainModel = VueFormGenerator.schema.createDefaultObject(schemaObj);
        const groupsModels = [];

        if (schemaObj.groups) {
          schemaObj.groups.forEach(group => {
            groupsModels.push(VueFormGenerator.schema.createDefaultObject(group));
          });
        }
        model = Object.assign(model, mainModel, ...groupsModels);
      });

      this.model = model;
    },
  },
};
