<template>
  <button class="app-button" :disabled="disabled" @click.prevent="buttonClick($event)">
    <slot name="icon"></slot>
    <span v-if="$slots.default" class="app-button__text">
      <slot></slot>
    </span>
  </button>
</template>

<script>
export default {
  name: 'AppButton',
  props: {
    disabled: Boolean,
  },
  methods: {
    buttonClick(event) {
      if (event.type === 'click') {
        this.$emit('click', event);
      }
    },
  },
};
</script>

<style lang="scss" scoped src="./app-button.scss"></style>
