<template>
  <div>
    <h2>Темы для изучения</h2>
    <vue-form-generator :schema="schema.schemaTopics" :model="model" :options="formOptions" tag="div" />
    <h2>Список экзаменов</h2>
    <vue-form-generator :schema="schema.schemaExams" :model="model" :options="formOptions" tag="div" />
    <h2>Письменная работа</h2>
    <vue-form-generator :schema="schema.schemaPaperwork" :model="model" :options="formOptions" tag="div" />
    <h2>Заключение</h2>
    <vue-form-generator :schema="schema.schemaConclusion" :model="model" :options="formOptions" tag="div" />
  </div>
</template>

<script>
import formMixins from '@/mixins/formMixins';

import schemaTopics from './form-example-schema/schemaTopics';
import schemaExams from './form-example-schema/schemaExams';
import schemaPaperwork from './form-example-schema/schemaPaperwork';
import schemaConclusion from './form-example-schema/schemaConclusion';

export default {
  name: 'FormExample',
  mixins: [formMixins],
  data() {
    return {
      schema: { schemaTopics, schemaExams, schemaPaperwork, schemaConclusion },
      formOptions: {
        validateAfterLoad: true,
        validateAfterChanged: true,
      },
    };
  },
  mounted() {
    this.generateFormModel();
  },
};
</script>

